
export default {
    components: {
    },
    data() {
        return {
            login:{
                name:"",
                psw:""
            },
            firstAlive:false,
            secAlive:false,
            step:"one",
            canSend:true,
            send:"点击获取",
            isUser:true

        }

    },
    mounted() {

    },
    methods: {
        nextStep(){
            this.next= true;
        },
        firstSubmit(){
            this.step = "two";
        },
        sendCode(){
            const that = this;
            let time = 60;
            if(!this.canSend) return;
            this.t = setInterval(() => {
                that.canSend = false;
                time--;
                that.send = time + "s";
                if (time < 1) {
                    window.clearInterval(that.t);
                    that.send = "重新发送";
                    that.canSend = true;
                }
            }, 1000);
        },
    //    第二部提交
        secSubmit(){

        }
    }
}